:root {
    --brandingColor1: #1d76ff;
    --brandingColor2: #ff9c74;
    --textColor1: #fff;
    --textColor2: #2e2e2e;
}

html {
    scroll-behavior: smooth;
}

body {
    margin: 0;
    height: 100%;
    font-family: 'Noto Sans JP', sans-serif;
}

a{
    color: var(--brandingColor2);
    text-decoration: none;
}
a:hover{
    text-decoration: underline;
}

.site-masthead {
    padding: 25px 0px;
}

.results-border{
    background-color: var(--brandingColor2);
    height: 10px;
    width: 100%;
}

.centered-container {
    margin: auto;
    width: 50%;
}

.padded-container, .padded-container-v25 {
    padding: 5px 25px;
}
.padded-container-v15 {
    padding: 5px 15px;
}

.thirds-sizing {
    width: 33.33%;
}

.flex-row-container {
    display: flex;
    flex-direction: row;
}

.flex-col-container {
    display: flex;
    flex-direction: column;
}

.social-buttons {
    user-select: none;
    width: 50%;
    height: 100%;
    text-align: right;
    font-size: 30pt;
}

.socialButton {
    color: var(--textColor1);
    margin-left: 5px;
}

.socialButton:hover {
    color: var(--brandingColor2);
}

.itemSeparator {
    color: var(--brandingColor2);
    margin-left: 5px;
}

.branding {
    width: 50%;
    margin: auto 0;
}

.branding img {
    user-select: none;
    width: 100%;
}

.branding h1 {
    display: inline;
    font-weight: bolder;
    user-select: none;
    font-size: 30pt;
    color: var(--textColor1);
}

.boxshadow-outset {
    -webkit-box-shadow: 3px 3px 11px -2px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 3px 3px 11px -2px rgba(0, 0, 0, 0.75);
    box-shadow: 3px 3px 11px -2px rgba(0, 0, 0, 0.75);
}

.boxshadow-inset {
    -webkit-box-shadow: inset 0px -7px 11px -9px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: inset 0px -7px 11px -9px rgba(0, 0, 0, 0.75);
    box-shadow: inset 0px -7px 11px -9px rgba(0, 0, 0, 0.75);
}

.textshadow {
    text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.30);
}

.message-box{
    margin: 25px auto;
}

.message-box h1 {
    margin: 0;
    color: var(--brandingColor2);
}

.message-box p {
    margin: 0px 5px 10px 5px;
    color: var(--textColor1);
}

.explain-box {
    padding: 5px 15px;
    display: flex;
    flex-direction: row;
}

[type="file"] {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    overflow: hidden;
    padding: 0;
    position: absolute !important;
    white-space: nowrap;
    width: 1px;
}

.explain-card {
    user-select: none;
    text-align: center;
    position: relative;
    margin: 8px;
    width: 33%;
    height: 175px;
}

.widget-enabled i, .widget-enabled div, .no-file-selected i, .no-file-selected div {
    color: var(--textColor1);
}

.file-selected i, .file-selected div {
    color: var(--brandingColor2);
}

.widget-disabled i, .widget-disabled div {
    opacity: 0.25;
}

.clickable:hover {
    cursor: pointer;
    opacity: 1;
}

.explain-card i {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 100pt;
    opacity: 0.2;
}

div.explain-card>div, label.explain-card>div, Button.explain-card>div {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    padding-top: 15%;
}

Button.explain-card {
    border: none;
    background-color: transparent;
    outline: none;
}

Button.explain-card h1{
    font-size: 2em;
    font-weight: bolder;
}
Button.explain-card p{
    font-size: 16px;
}

/* Effects & Animations */

.glow {
    animation: iconPulse var(--duration) infinite;
    animation-direction: alternate;
}

.bounce {
    animation: iconBounce var(--duration) infinite;
    animation-direction: alternate;
}

@keyframes iconPulse {
    0% {
        opacity: 0.3;
    }
    100% {
        opacity: 0.1;
    }
}

@keyframes iconBounce {
    0% {
        bottom: 0px;
    }
    100% {
        bottom: 50px;
    }
}

::selection {
    background: var(--brandingColor2);
}

::-moz-selection {
    background: var(--brandingColor2);
}

/* Responsivity */

@media (max-device-width: 411px) {
    .centered-container, .message-box, .explain-box {
        width: 90%;
    }
    .explain-box {
        flex-direction: column;
    }
    .explain-card {
        margin-top: 15px;
        height: 250px;
        width: 100%;
    }
    .explain-card i {
        font-size: 175pt;
    }
    .explain-card>div>h1 {
        font-size: 40pt;
    }
    .explain-card>div>p {
        font-size: 25pt;
    }
    div.explain-card>div {
        padding-top: 0%;
    }
    .branding h1 {
        font-size: 40pt;
    }
    .social-buttons {
        font-size: 50pt;
    }
}